export default {
	name: 'uSearch',
	computed: {
		searchArr (){
			var that = this;
			var newArr = []
			var len = that.dictArr.length
			if(!that.searchKey){
				for (var i=0;i<len;i++) {
					if(that.dictArr[i].indexOf(that.searchModel)>-1){
						newArr.push(that.dictArr[i])
					}
				}
			}else{
				for (var i=0;i<len;i++) {
					if(that.dictArr[i][that.searchKey].indexOf(that.searchModel)>-1){
						newArr.push(that.dictArr[i])
					}
				}	
			}
			return newArr
		}
	},
	props: {
		"dictArr": {
			type: Array,
			default () {
				return []
			}
		},
		"searchKey": {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			searchModel: ''
		}
	},
	created(){
		
	},
	mounted() {
		
	},
	methods: {
		searchInput(e){
			this.searchModel = e.detail.value
		},
		getSearchInputValue(){
			return this.searchModel
		},
		selectIndex(val){
			this.$emit('selectIndexValue', val);
		}
	}
}
